import moment from 'moment'
import React from 'react'

export default function Details({
  firstName,
  lastName,
  email,
  mobile,
  userFrom,
  address
}) {
  return (
    <div className='card mb-3'>
      <div className='card-body'>
        <div className='row'>
          <div className='col-sm-3'>
            <h6 className='mb-0'>Full Name</h6>
          </div>
          <div className='col-sm-9 text-secondary'>
            {firstName + ' ' + lastName}
          </div>
        </div>
        <hr />
        <div className='row'>
          <div className='col-sm-3'>
            <h6 className='mb-0'>Email</h6>
          </div>
          <div className='col-sm-9 text-secondary'>{email}</div>
        </div>
        <hr />
        <div className='row'>
          <div className='col-sm-3'>
            <h6 className='mb-0'>Mobile</h6>
          </div>
          <div className='col-sm-9 text-secondary'>{mobile}</div>
        </div>
        <hr />
        <div className='row'>
          <div className='col-sm-3'>
            <h6 className='mb-0'>User From</h6>
          </div>
          <div className='col-sm-9 text-secondary'>
            {moment(userFrom).format('MMM Do YY')}
          </div>
        </div>
        <hr />
        <div className='row'>
          <div className='col-sm-3'>
            <h6 className='mb-0'>Address</h6>
          </div>
          <div className='col-sm-9 text-secondary'>{address}</div>
        </div>
      </div>
    </div>
  )
}
